import React, { Component } from 'react';
import Modal from 'react-awesome-modal';
import '../Modal/Style.css'
import NewProduct from '../newProduct/NewProduct';

export default class ProductModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visible : false
        }
    }

    openModal() {
        this.setState({
            visible : true
        });
    }

    closeModal() {
        this.setState({
            visible : false
        });
    }

    render() {
        return (
            <section>
               
                <input className="ModelOpen"  type="button" value="+ New Product" onClick={() => this.openModal()} />
                <Modal   visible={this.state.visible}  width="600"  height="900" effect="fadeInLeft" onClickAway={() => this.closeModal()}>
                    <div className="Modal">
                        <NewProduct/>
                        <button className="ModelClose" onClick={() => this.closeModal()}>Close</button>
                    </div>
                </Modal>
            </section>
        );
    }
}