import React, { useState } from 'react'
import "./userList.css"
import { DataGrid } from '@material-ui/data-grid';
import DeleteIcon from '@material-ui/icons/Delete';
import { UserRows } from '../../DummyData/DummyData';
import { Link } from 'react-router-dom';
import Examples from '../Modal/Modal';


function UserList() {
    const [data, setData] = useState(UserRows)
    const handleClickDelete=(id)=>{
        setData(data.filter((item)=>item.id !== id))
    }
   
    const columns = [
        { field: 'PIC', headerName: 'PIC ', width: 100 ,renderCell:(params)=>{  
            return(    
                <div className="UserListuser">
                    <img className="UserListImg" src={params.row.avatar} alt="" /> 
                </div>     
            )
       
        }},
        { field: 'UserName', headerName: 'UserName ', width: 200 },
        { field: 'Email', headerName: 'Email ', width: 230 },
        { field: 'Transaction', headerName: 'Transaction ', width: 160,font:16},
        { field: 'status', headerName: 'status ', width: 130 },
        { field: 'action', headerName: 'Action ', width: 300,renderCell:(params)=>{
            return(
                <>
                <Link to={"/user/"+params.row.id}>
                <i className="pen fas fa-pen" />
                </Link>
                    <DeleteIcon className="BtnListDelete" onClick={()=>handleClickDelete(params.row.id)}/>                   
                </>    
            )
        } },
      ]  
    return (
        <div className="userList">
            <div className="BrandsbtnGroup">
            <div>
            </div>
           <Examples/>
            
        </div>
            <div className="UserListTable">
                <DataGrid 
                rows={data}
                columns={columns} 
                pageSize={12}
                disableSelectionOnClick     
                autoHeight
                    />    
        </div>
        </div>
    )   
   
}

export default UserList
